<template>
	<div v-my-directive data-title="" class="usermain">
		<!--  -->
		<userleft></userleft>
		<div class="CatalogLayout">
			<!--  -->
			<div class="Genericpadding bold"></div>

			<div v-if="form" class="Genericpadding articlebt">
				<div class="Horizontalspacing interval">
					<el-avatar :size="60" :src="form.Avatar">
						<div class="image-slot iconfont icon-setting-role"></div>
					</el-avatar>
					<div class="ThinLineLayout">
						<div class="bold">{{ user.username }}</div>
						<div class="center minfont grey">: 100100px: .jpg.gif.png500kb</div>
					</div>
				</div>
				<el-upload class="upload-demo center" :http-request="Pictures" action="#" :show-file-list="false" :before-upload="handleBeforeUpload">
					<el-button type="primary"></el-button>
				</el-upload>
			</div>
			<div class="Genericpadding bold helf">
				<el-form v-if="form" :rules="rules" ref="form" :model="form" label-width="80px">
					<el-form-item label="">
						<el-input style="width: 300px" v-model="form.username" :disabled="true"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input style="width: 300px" v-model="form.name" :disabled="true"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input style="width: 300px" v-model="form.email" :disabled="true"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input style="width: 300px" v-model="form.Creationtime" :disabled="true"></el-input>
					</el-form-item>
					<el-form-item label="" prop="nickname">
						<el-input style="width: 300px" v-model="form.nickname" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="QQ" prop="QQ">
						<el-input style="width: 300px" v-model="form.QQ" placeholder="qq"></el-input>
					</el-form-item>
					<el-form-item label="" prop="WeChat">
						<el-input style="width: 300px" v-model="form.WeChat" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="telegram" prop="telegram">
						<el-input style="width: 300px" v-model="form.telegram" placeholder="telegram"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input style="width: 500px" resize="none" type="textarea" placeholder="" v-model="form.signature" :autosize="{ minRows: 2, maxRows: 2 }" maxlength="30" show-word-limit> </el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="submitForm('form')"></el-button>
					</el-form-item>
				</el-form>
			</div>
		</div>
	</div>
</template>
<script>
import userleft from './../components/userleft'
import { dataRequest as api, filesRequest, automaticLogin } from '@/api/api'
import { mapState } from 'vuex'
export default {
	name: 'name',
	computed: {
		...mapState(['user']),
		mapboxMap1() {
			// return this.$store.state.mapbox.map;

			return this.user
		},
	},
	components: {
		// 
		userleft,
	},
	data() {
		return {
			//
			form: '',
			rules: {
				nickname: [
					{ required: false, message: 'QQ', trigger: 'blur' },
					{ min: 3, max: 16, message: ' 3  16', trigger: 'blur' },
				],
				QQ: [
					{ required: false, message: 'QQ', trigger: 'blur' },
					{ validator: this.validateQQ, trigger: 'blur' },
				],
				WeChat: [
					{ required: false, message: '', trigger: 'blur' },
					{ validator: this.validateWeChat, trigger: 'blur' },
				],
				telegram: [
					{ required: false, message: 'Telegram', trigger: 'blur' },
					{ validator: this.validateTelegram, trigger: 'blur' },
				],
			},
		}
	},
	methods: {
		// 
		handleBeforeUpload(file) {
			const maxSize = 500 * 1024 // 500 KB in bytes
			if (file.size > maxSize) {
				this.$message.error(' 500 KB')
				return false // 
			}

			const allowedExtensions = ['jpg', 'jpeg', 'png', 'gif']
			const allowedMimeTypes = ['image/jpeg', 'image/png', 'image/gif']

			const fileExtension = file.name.split('.').pop().toLowerCase()
			const fileType = file.type.toLowerCase()

			if (!allowedExtensions.includes(fileExtension) || !allowedMimeTypes.includes(fileType)) {
				this.$message.error('')
				return false // 
			}

			return true // 
		},
		async Pictures(obj) {
			console.log(obj)
			var formData = new FormData()
			formData.append('file', obj.file)

			let arr = await filesRequest(['post=pictureupload', formData])
			if (arr.code == 200) {
				// 
				automaticLogin()
				this.$message.success(arr.massage)
			} else {
				this.$message.error(arr.massage)
			}
		},

		async submitForm(formName) {
			this.$refs[formName].validate(async valid => {
				if (valid) {
					// 
					let arr = { nickname: this.form.nickname, QQ: this.form.QQ, WeChat: this.form.WeChat, telegram: this.form.telegram, signature: this.form.signature }
					arr = await api(['post=Datamodification', arr])
					if (arr.code == 909 || arr.code == 4004) {
						this.$message.error('')
					} else if (arr.code == 200) {
						this.$message.success('')
						automaticLogin()
					} else {
						this.$message.error('')
					}
					console.log(arr)
				} else {
					// 
					console.log('error submit!!')
					return false
				}
			})
		},
		// 
		validateQQ(rule, value, callback) {
			if (!value) {
				// 
				callback()
			} else {
				const regExp = /^[1-9]\d{5,13}$/
				if (regExp.test(value)) {
					callback()
				} else {
					callback(new Error('QQ513'))
				}
			}
		},
		validateWeChat(rule, value, callback) {
			if (!value) {
				// 
				callback()
			} else {
				// 620
				const regExp = /^[a-zA-Z][a-zA-Z0-9_-]{5,19}$/
				if (regExp.test(value)) {
					callback()
				} else {
					callback(new Error('620'))
				}
			}
		},
		validateTelegram(rule, value, callback) {
			if (!value) {
				// 
				callback()
			} else {
				// Telegram532
				const regExp = /^[a-zA-Z][a-zA-Z0-9_-]{4,31}$/
				if (regExp.test(value)) {
					callback()
				} else {
					callback(new Error('Telegram532'))
				}
			}
		},
	},
	mounted() {
		// :
		this.form = this.user
	},
	watch: {
		mapboxMap1(newData, oldData) {
			console.log(newData, 'jiu', oldData)
			this.form = newData
		},
	},
}
</script>
<style scoped>
.image-slot {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 60px;
	height: 60px;
	background: var(--el-fill-color-light);
	color: var(--el-text-color-secondary);
	font-size: 30px;
}
.helf {
	height: 100%;
}
</style>
