<template
	><main v-my-directive :data-title="content.title" :data-description="content.description">
		<div v-loading.fullscreen.lock="loading" class="QAacHead">
			<div class="QAacnav articlebt line">
				<div class="columnLayout interval">
					<div class="Horizontalspacing interval minfont">
						<div class="Horizontalspacing minixgap">
							<img class="pimgmins" src="./../assets/img/avatar.png" alt="" />
							<span class="center">{{ content.username }}</span>
						</div>
						<div class="center grey">{{ content.Creationtime }}</div>
						<div class="center grey">:</div>
					</div>
					<span class="maxqcfont">{{ content.title }}</span>
					<div class="Horizontalspacing interval minfont">
						<div class="qzbuttons" @click="Clicktoreply()"> <div>+</div><span></span> </div>
						<div class="qathumbs-up" @click="Favoritelikes()">
							<div class="iconfont icon-31dianzan" :class="userlike ? 'icon-dianzan_kuai' : 'icon-31dianzan'"></div>
							<div v-if="userlike"></div>
							<div v-else></div>
							<div v-if="collect > 0">{{ collect }}</div>
						</div>
						<div class="Horizontalspacing minixgap center">
							<div class="redborad"></div>
							<div v-if="Bestanswer" class="center">~</div>
							<div v-else class="center"></div>
						</div>
						<div v-if="content.Price > 0" class="Horizontalspacing minixgap center">
							<div class="iconfont icon-dashangzonge red"></div>
							<div class="center"
								><span class="red">{{ content.Price }}</span
								><span v-if="Bestanswer"></span>
							</div>
						</div>
					</div>
				</div>
				<div class="Horizontalspacing minixgap center"
					><span></span><span> {{ content.PageView }}</span></div
				>
			</div>
		</div>
		<div class="main">
			<div>
				<div class="content">
					<!--  -->
					<div class="Genericpadding columnLayout">
						<div class="QArectangle articlebt">
							<div class="qzsolld qzsolldav Green"></div>
						</div>
						<!--  -->
						<div class="columnLayout interval">
							<Reply v-if="content.content" :content="content.content" width="750px"></Reply>
							<!--  -->
							<div v-if="Bestanswer" class="abstracts columnLayout interval">
								<div class="articlebt">
									<div class="Horizontalspacing minaxgap red bold">
										<div class="iconfont icon-dianzan_kuai maxqcfont"></div>
										<div></div>
									</div>
									<div class="Horizontalspacing interval minfont">
										<div class="Horizontalspacing minixgap">
											<img class="pimgmins center" src="./../assets/img/avatar.png" alt="" />
											<span class="center">{{ Bestanswer.username }}</span>
										</div>
										<div v-if="content.Price > 0" class="Horizontalspacing minixgap center">
											<div class="iconfont icon-dashangzonge red"></div>
											<div class="center grey"
												><span class="red">{{ content.Price }}</span
												></div
											>
										</div>
									</div>
								</div>
								<!--  -->
								<Reply :content="Bestanswer.content" width="800px"> </Reply>
							</div>
							<div class="abstract Horizontalspacing minxgap grey minifont">
								<div class="iconfont icon-gantanhao-yuankuang"></div>
								<div>{{ domain }}</div>
							</div>
						</div>
					</div>
					<!--  -->
					<div class="ThinLineLayout">
						<div class="Genericpadding articlebt">
							<div></div>
							<div class="minfont grey">5</div>
						</div>
						<div v-if="logon" id="Clicktoreply" class="Genericpadding Horizontalspacingg interval">
							<img src="./../assets/img/avatar.png" alt="" class="pimg" />
							<div class="Intervalvertical">
								<div>
									<div>
										<!--  -->
										<dexedi height="100px" width="760px" ref="child"></dexedi>
									</div>
								</div>
								<div class="buttons" @click="processReply()"></div>
							</div>
						</div>
						<div v-if="Replylist[0]" class="Genericpadding Intervalvertical">
							<!--  -->
							<div v-for="(item, index) in Replylist" :key="index" class="Intervalvertical">
								<div class="articlebt minfont">
									<div class="Horizontalspacing minxgap"
										><img src="./../assets/img/avatar.png" alt="" class="pimgmin" />
										<p class="center">{{ item.username }}</p>
										<el-button v-if="!Bestanswer" type="primary" @click="adopt(item.id)" plain></el-button>
										<div v-if="item.optimal == 1" class="iconfont icon-zuijiadaan"></div>
									</div>
									<div class="Horizontalspacing minxgap grey">
										<p>{{ item.Creationtime }}</p>
										<div @click=";(CommentID = item.id), (CommentIDname = item.username), Clicktoreply()"></div>
									</div>
								</div>
								<div class="abstracts pmaring TopRectangles">
									<Reply :content="item.content" width="775px"> </Reply>
								</div>
								<!--  -->
								<ul v-if="item.child" class="pmaring Intervalvertical">
									<div v-for="(item, index) in item.child" :key="index" class="Intervalvertical">
										<div class="articlebt minfont">
											<div class="Horizontalspacing minxgap grey"
												><img src="./../assets/img/avatar.png" alt="" class="pimgmins" />
												<p class="center">{{ item.username }}</p>
												<div class="iconfont icon-xiangyou minifont center"></div>
												<p>{{ item.superiorname }}</p>
											</div>
											<div class="Horizontalspacing minxgap grey">
												<p class="center">{{ item.Creationtime }}</p>
												<div @click=";(CommentID = item.id), (CommentIDname = item.username), Clicktoreply()"></div>
											</div>
										</div>
										<div class="abstracts pmaring">
											<Reply :content="item.content" width="750px"> </Reply>
										</div>
									</div>

									<!--      -->
								</ul>
							</div>
						</div>

						<div v-if="Replylist.length < 1" class="Genericpadding">
							<div class="abstracts">
								<p class="center">~</p>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div>
				<qaleft></qaleft>
			</div>
		</div>
	</main>
</template>
<script>
import { mapState } from 'vuex'
import qaleft from './../components/qaleft'
import dexedi from './../components/dexedi'
import { dataRequest as api, setText } from '@/api/api'
import Reply from './../components/Reply'
export default {
	name: 'name',
	computed: {
		...mapState(['logon', 'user']),
	},
	components: {
		// 
		qaleft,
		dexedi,
		Reply,
	},
	data() {
		return {
			loading: true,
			domain: window.location.href,
			articid: this.$route.params.index.replace(/.html/g, ''),
			content: [],
			Replylist: '',
			CommentID: '',
			Bestanswer: false,
			// 
			collect: 0,
			userlike: false,
			//
		}
	},
	methods: {
		async get() {
			let array = await api(['post=getquestions', { id: this.articid }])

			if (array.code == 200) {
				this.content = array.data[0]
				this.collect = this.content.collect.length
				this.content.Price = (Math.round(this.content.Price * 1000) / 1000).toString()
			}
			this.getprocessReply()
			if (this.user) {
				this.getFavoritelikes()
			}
		},

		// 
		async processReply() {
			let value = this.$refs.child.delta()
			let delta = value[0]
			if (setText(value[1])) {
				// 
				let arr = { PostID: this.articid, userid: this.user.id, CommentID: this.CommentID, content: delta.ops }
				arr = await api(['post=Postanswer', arr])

				if (arr.code == 200) {
					this.getprocessReply()
					this.$message.success('')
				}
			} else {
				// 
				this.$message.error('')
			}
			this.CommentID = ''
		},
		//  
		async getprocessReply() {
			let arr = await api(['post=getanswer', { PostID: this.articid }])

			if (arr.code == 200) {
				this.Replylist = arr.data
				for (let i = 0; i < this.Replylist.length; i++) {
					if (this.Replylist[i].optimal == 1) {
						this.Bestanswer = this.Replylist[i]
						this.Replylist.splice(i, 1)
						this.Replylist.unshift(this.Bestanswer)
					}
				}
			}
		},
		// 
		Clicktoreply() {
			const element = document.getElementById('Clicktoreply')
			const distanceToTop = element.offsetTop
			window.scrollTo({ behavior: 'smooth', top: distanceToTop })
			// Clicktoreply
		},
		// 
		async adopt(ele) {
			this.$confirm(', ?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning',
			})
				.then(async () => {
					// ID  ID  ID
					// ID ID ID  IDID   ID
					let arr = { articid: this.articid, CommentID: ele }
					arr = await api(['post=adopt', arr])
					if (arr.code == 200) {
						this.$message({
							type: 'success',
							message: '!',
						})
					} else {
						this.$message.error(arr.data)
					}
				})
				.catch(() => {
					this.$message({
						type: 'info',
						message: '',
					})
				})
		},
		//  
		async Favoritelikes() {
			if (this.user) {
				let arr = await api(['post=qaection', { PostID: this.articid }])
				this.userlike = !this.userlike
				this.collect = this.userlike ? this.collect / 1 + 1 : this.collect / 1 - 1
			} else {
				// 
				this.$message.error('')
			}
		},
		// 
		async getFavoritelikes() {
			this.content.collect.forEach(element => {
				if (element.userid == this.user.id) {
					this.userlike = true
				}
			})
		},
	},
	mounted() {
		// :
		this.get()
	},
	updated() {
		this.loading = false
	},
}
</script>
<style scoped>
.icon-zuijiadaan {
	font-size: 25px !important;
	color: #f30 !important;
	font-weight: 300 !important;
}
.el-button--primary {
	height: 25px !important;
	padding: 5px !important;
	font-size: 12px !important;
}
</style>
