<template>
	<div v-if="!logon" class="LoginRegistration bold">
		<!--  -->
		<div class="center" @click=";(logonbox = true), (homelogon = true)"></div>
		<div class="center" @click=";(logonbox = true), (register = true)"></div>
	</div>
	<!--  -->
	<div v-else class="Afterloggingin positioned center">
		<div class="Headcircle">
			<el-avatar class="Roundheadportrait" :size="60" v-if="user" :src="user.Avatar">
				<div class="image-slot iconfont icon-setting-role"></div>
			</el-avatar>
			<el-avatar v-else class="Roundheadportrait" :size="60">
				<div class="image-slot iconfont icon-setting-role"></div>
			</el-avatar>
			<!-- <img src="./../assets/img/avatar.png" alt="" class="Roundheadportrait" /> -->
			<div class="iconfont icon-renzhengguanli authentication YellowLabelCertification"></div>
		</div>
		<!-- Genericpadding  -->
		<div class="Popupafterlogin">
			<div class="Popupafterlogins columnLayout interval">
				<div class="dropdown-box"></div>
				<div class="dropdown-boxr"></div>
				<div class="Horizontalspacing minaxgap zindex">
					<div class="Headcircle">
						<el-avatar class="Roundheadportrait" :size="60" v-if="user" :src="user.Avatar">
							<div class="image-slot iconfont icon-setting-role"></div>
						</el-avatar>
						<el-avatar v-else class="Roundheadportrait" :size="60">
							<div class="image-slot iconfont icon-setting-role"></div>
						</el-avatar>
						<!-- <img src="./../assets/img/avatar.png" alt="" class="Roundheadportrait" />  -->
					</div>
					<span class="center">{{ user.username }}</span
					><span class="center Whitebox minifont Green padding5">{{ user.name }}</span>
				</div>
				<div class="Whitebox padding10 grey minfont articlebt">
					<div class="Horizontalspacing minxgap"><span>:</span><span>0.0</span></div>
					<div class="Horizontalspacing minxgap"><span>:</span><span>0.0</span></div>
				</div>
				<div class="userbuttons zindex">
					<span class="center">{{ $t('message.PersonalCenter') }}</span>
				</div>
				<div class="Whitebox padding20 grey minfont Horizontalspacing space-around zindex">
					<a class="turngreen" href="http://" target="_blank" rel="noopener noreferrer"></a>
					<a class="turngreen" href="http://" target="_blank" rel="noopener noreferrer"></a>
					<a class="turngreen" href="http://" target="_blank" rel="noopener noreferrer"></a>
					<p class="turngreen" @click="exit"></p>
				</div>
			</div>
		</div>
	</div>
	<!--    -->
	<div v-if="logonbox && !logon" @click.self=";(logonbox = false), (homelogon = false), (register = false), (Retrievepassword = false), (Retrievepasswordverification = false), (resetpassword = false)" class="logonbox">
		<div id="logonmanbox" class="center logonmanbox columnLayout interval">
			<div class="columnLayout minaxgap"><img src="./../assets/logo.svg" alt="" class="logonimg center" /><span class="center">Hi~ DEXC</span></div>
			<form v-if="homelogon" id="homelogon" autocomplete="off" class="columnLayout interval paddingtop10">
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-yonghu grey"></div><input class="Searchinput" type="text" v-model="account" placeholder="" /> </div>
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-tianchongxing- grey"></div><el-input class="denglucss" placeholder="" v-model="password" show-password></el-input> </div>
				<div class="Horizontalspacing space-around minfont">
					<div class="Horizontalspacing minxgap"><span class="grey">?</span><span @click=";(register = true), (homelogon = false)">!</span></div
					><span @click=";(Retrievepassword = true), (homelogon = false)">?</span>
				</div>
				<div class="loginbutton" @click="login"></div>
			</form>
			<!--  -->
			<!--  -->

			<form autocomplete="off" v-if="register" class="columnLayout interval paddingtop10">
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-yonghu grey"></div><input class="Searchinput" name="register" data-name="username" type="text" placeholder="" /> </div>

				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-tianchongxing- grey"></div><el-input placeholder="" @input="fun" v-model="input" show-password></el-input> </div>
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-tianchongxing- grey"></div><el-input placeholder="" v-model="input" show-password></el-input> </div>
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-youxiang grey"></div><input class="Searchinput" name="register" data-name="email" type="mail" placeholder="" /> </div>
				<div class="loginbutton" onclick="register()"></div>
				<div class="minfont center">
					<div class="Horizontalspacing minxgap"><span class="grey">?</span><span @click=";(homelogon = true), (register = false)">!</span></div>
				</div>
			</form>
			<!--   -->
			<form id="Retrievepassword" v-if="Retrievepassword" autocomplete="off" class="columnLayout interval paddingtop10">
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-yonghu grey"></div><input class="Searchinput" type="text" placeholder="" /> </div>
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-youxiang grey"></div><input class="Searchinput" name="Retrieve" data-name="email" type="mail" placeholder="" /> </div>
				<div class="loginbutton" @click=";(Retrievepassword = false), (Retrievepasswordverification = true)"></div>
			</form>
			<!--  -->
			<form id="Retrievepasswordverification" v-if="Retrievepasswordverification" autocomplete="off" class="columnLayout interval paddingtop10">
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-tianchongxing- grey"></div><input class="Searchinput" type="text" placeholder="" /> </div>
				<div class="loginbutton" @click=";(Retrievepasswordverification = false), (resetpassword = true)"></div>
			</form>
			<!--  -->
			<form id="resetpassword" v-if="resetpassword" autocomplete="off" class="columnLayout interval paddingtop10">
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-tianchongxing- grey"></div><input class="Searchinput" name="Retrieve" data-name="password" type="password" placeholder="" autocomplete="new-password" /> </div>
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-tianchongxing- grey"></div><input class="Searchinput" name="Retrieve" data-name="Confirm" type="password" placeholder="" autocomplete="new-password" /> </div>
				<!--  -->
				<div class="loginbutton"></div>
			</form>
		</div>
	</div>
</template>
<script>
import { mapState } from 'vuex'
import { dataRequest as api, automaticLogin, Logoutoflogin } from '@/api/api'
import sha256 from 'crypto-js/sha256'
export default {
	name: 'name',
	computed: {
		...mapState(['logon', 'user']),
	},
	components: {
		// 
	},
	data() {
		return {
			// 
			account: '',
			password: '',
			//
			logonbox: false,
			// 
			homelogon: false,
			// 
			register: false,
			// 
			Retrievepassword: false,
			Retrievepasswordverification: false,
			resetpassword: false,
			//
		}
	},
	methods: {
		// 
		//JWT     
		//JWT
		async login() {
			if (this.account.replace(/\s/g, '') && this.password.replace(/\s/g, '')) {
				// let arr = await logonapi(this.account, sha256(sha256(this.password)).toString())
				let arr = await api(['post=JWT', { username: this.account, password: sha256(sha256(this.password)).toString() }])

				// 
				console.log(arr)
				if (arr.code == 200) {
					// 

					// JWT
					const data = { id: 1, JWT: arr.data, logon: false }

					if (await this.$indexedDB.getObject(data.id)) {
						// 
						this.$indexedDB.updateObject(data)
					} else {
						//  
						this.$indexedDB.addObject(data)
					}
					// 
					await automaticLogin()
					this.logonbox = false
				} else {
					this.$message.error('')
				}
				//
			} else {
				this.$message.error('')
				console.log(this.logon)
			}
			// 
		},
		async automaticlogin() {
			let arr = await automaticLogin()
		},
		// 
		async exit() {
			Logoutoflogin()
		},
	},
	mounted() {
		//    
		//   
		// 
		this.automaticlogin()
	},
}
</script>
<style>
.denglucss .el-input__wrapper {
	height: 16px;
	border: none !important;

	box-shadow: none !important;
}
</style>
